}

.app-name {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 3% auto;
  background-color: #66ccfe;
  height: 450px;
  width: 100%;
  margin: 0 auto;
  font-family: "Nunito", sans-serif;
}

.app-name h1 {
  color: #ffcb4f;
  text-shadow: 1px 1px grey;
  font-family: "Nunito", sans-serif;
  letter-spacing: 18px;
  font-size: 8vw;
  padding: 15vh;
}

.searchBar-container {
  margin: 3% auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;

  font-family: "Nunito", sans-serif;
}
.searchBar-container input {
  width: 50%;
  margin-right: 3px;
  color: #22beb6;
  font-family: "Nunito", sans-serif;
}
.searchBar-container a {
  color: #22beb6;
}

.searchBar-container span {
  color: #22beb6;
  /* display: block; */
}

.searchBar-container select {
  display: inline;
}

.intro-container {
  text-align: center;
  vertical-align: middle;
  font-size: 25px;
  color: grey;
  font-family: "Nunito", sans-serif;
}

.category-title-container {
  margin: 5%;
}

.categorie-container {
  margin-left: 4%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

@media (max-width: 700px) {
  .categorie-container {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}

.categorie-container section {
  text-align: center;
  margin-top: -0.5%;
  margin-right: 1.5%;
  height: 150px;
  max-width: 200px;
  min-width: 200px;
  color: white;
  font-size: 20px;
  font-family: "Nunito", sans-serif;
}

.categorie-container :hover {
  /* opacity: 0.8; */
  -webkit-animation-name: stretch;
          animation-name: stretch;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes stretch {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

@keyframes stretch {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}
.deleteCategory {
  font-size: 10px;
  position: absolute;
  z-index: 1;
}

.categorie-container section i {
  font-size: 70px;
  color: white;
  padding: 3%;
}

